import SplitText from "@/components/motion/SplitText";
import Image from "next/image";
import { Metadata } from "next";

export const metadata: Metadata = {
  title: "Screenshots - Qalby Muslim",
  description: "Explore key screens of QalbyMuslim app on mobile devices.",
};

export default function ScreenshotsPage() {
  const screenshots = [
    {
      title: "Prayer Times",
      description: "View daily prayer schedules with accurate timings and easy-to-read layout, helping users stay connected to their spiritual routine effortlessly.",
      image: "https://images.unsplash.com/photo-1624863035482-b005dbba3b6f?auto=format&fit=crop&w=656&h=640",
    },
    {
      title: "Qibla Compass",
      description: "Navigate the Qibla direction precisely using the app's built-in compass, designed for clarity and quick orientation anywhere in the world.",
      image: "https://images.unsplash.com/photo-1504807959081-3dafd3871909?auto=format&fit=crop&w=656&h=446",
    },
    {
      title: "Quran Reader",
      description: "Read the Holy Quran with beautiful illuminated text and intuitive navigation.",
      image: "https://images.unsplash.com/photo-1562692885-d368c1d6e687?auto=format&fit=crop&w=656&h=640",
    },
    {
      title: "Zakat Calculator",
      description: "Calculate your Zakat accurately with current nisab values and easy input interface.",
      image: "https://images.unsplash.com/photo-1624863035482-b005dbba3b6f?auto=format&fit=crop&w=656&h=640",
    },
  ];

  return (
    <main className="container-max py-24">
      <div className="text-center mb-16">
        <SplitText
          as="h1"
          className="font-heading text-4xl md:text-5xl text-emerald"
          text="Gallery"
        />
        <p className="mt-4 text-emerald/80 text-lg">
          Explore key screens of QalbyMuslim app on mobile devices
        </p>
      </div>

      <div className="grid md:grid-cols-2 gap-8">
        {screenshots.map((screenshot, index) => (
          <div key={index} className="bg-white/5 backdrop-blur-sm border border-emerald/20 rounded-lg overflow-hidden">
            <div className="relative h-64">
              <Image
                src={screenshot.image}
                alt={screenshot.title}
                fill
                className="object-cover"
              />
            </div>
            <div className="p-6">
              <h3 className="font-heading text-xl text-emerald mb-2">{screenshot.title}</h3>
              <p className="text-emerald/80">{screenshot.description}</p>
            </div>
          </div>
        ))}
      </div>
    </main>
  );
}